body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Axis path,
.Axis line {
  stroke: #EFEFEF;
  stroke-width:2;
}

.Axis text {
  font-size: 16px;
  fill: black;
}

.Axis-Bottom text {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  text-anchor: end;
}

.line path {
  fill: transparent;
  stroke: rgba(155,155,155,0.8);
  stroke-width: 4;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.people_area{
  background-color: #FEFEFE;
  grid-area: people_area;
  padding: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width:100%;
  -webkit-align-items: center;
          align-items: center;
}

.label_area{
  background-color: #FEFEFE;
  grid-area: label_area;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.label_area .title {
  font-size: 32pt;
}

.label_area .suntitle {
  font-size: 12pt;
}

.plot_area{
  background-color: #DCDCDC;
  grid-area: plot_area;
}

.table_area{
  background-color: #FEFEFE;
  grid-area: table_area;
  width: 100%;
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.table_area table{
  width: 100%;
}

.table_area th, .table_area td {
  padding: 12px;
  text-align: middle;
  background-color: #F5F5F5;
}

.table_area th {
  background-color: #4CAF50;
  color: white;
}

.table_area tr:nth-child(even) {background-color: #f2f2f2;}

.actual_payment_class {
  text-align: left;
  font-weight: bold;
  width: 20ex;
}

.summary_area{
  background-color: #F0F;
  grid-area: summary_area;
}

.model_choice{
  text-align: left;
  float: left;
  padding: 10px 10px;
  width: 100%;
  border: 1px solid #000;
}
.grid-container {
  display: grid;
  margin: 20px;
  border: 1px solid #444;
  grid-template-areas:
    'people_area people_area label_area'
    'plot_area plot_area table_area'
    'summary_area summary_area summary_area'
}

